<template>
  <div class="jurCurrentState">
    <div class="timeline" v-if="jurCurrentStateData.length !== 0">
      <div class="timeline-item" v-for="item in jurCurrentStateData" :key="item.id">
        <div class="tbody timeline-left">
          <span class="timeline-date">{{item.creationDate}}</span>
          <i class="timeline-icon"></i>
        </div>
        <div class="tbody timeline-right">
          [<router-link class="opName"
            :to="{name: 'jurCurrentState', params: { id: $route.params.objId } }"
          >{{ item.role }}</router-link>]
          <span class="userName">{{ item.user }}</span>
          <span class="flowOp">{{ item.flowOp}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getSearchFlowLogByObjId } from "@/api/user";
import moment from "moment";
export default {
  name: "JurCurrentState",
  data() {
    return {
      jurCurrentStateData: []
    };
  },
  mounted() {
    debugger
    if (!this.$route.params.objId) {
      this.$router.push({path: '/jurHistory'})
    } else {
      sessionStorage.setItem("objId", JSON.stringify(this.$route.params));
    }
    this.loadDataHandler();
  },
  methods: {
    moment: moment,
    loadDataHandler() {
      const { objId } = JSON.parse(sessionStorage.getItem("objId"))
      getSearchFlowLogByObjId(objId).then(resp => {
        const content = resp.data.content;
        for (var i = 0, len = content.length; i < len; i++) {
          this.jurCurrentStateData.push({
            id: content[i].id,
            creationDate: moment(content[i].creationDate).format(
              "YYYY-MM-DD HH:mm:ss"
            ),
            role: content[i].role.description,
            flowOp: content[i].flowOp,
            user: content[i].user.userName
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.flowOp {
  margin-left: 5px;
}
.jurCurrentState {
  width: 480px;
  text-align: left;
  margin: 0 auto;
  margin-top: 20px;
  .timeline-item {
    display: flex;
    position: relative;
    .opName {
      color: #2f8ade;
      font-weight: bold;
    }
    .timeline-left {
      &:after {
        content: "";
        display: block;
        position: absolute;
        right: 0;
        top: 11px;
        border-right: 1px solid #ccc;
        height: 100%;
        z-index: 1;
      }
    }
    .tbody {
      padding: 11px 20px;
      position: relative;
      .timeline-icon {
        display: inline-block;
        width: 18px;
        height: 18px;
        border-radius: 50px;
        background-color: #f7f9fb;
        position: absolute;
        right: -9px;
        top: 11px;
        z-index: 9;
        border: 3px solid #2f8ade;
      }
    }
  }
}
</style>